@charset "UTF-8";

#shot-chart {
  width: 100%;
  margin-top: -38px;
  margin-left: auto;
  margin-right: auto;
}

/********SHOTCHART COURT STYLES****** */
#shot-chart-container {
  margin-top: -22%;
  margin-bottom: 5%;
}

@media (min-width: 50em) {
  #shot-chart-container {
    margin-top: -15%;
  }
}

@media (min-width: 55em) {
  #shot-chart-container {
    margin-top: -10%;
  }
}

@media (min-width: 78em) {
  #shot-chart-container {
    padding-left: 1em;
    margin-left: 33%;
  }
}

#shot-chart-container .shot-chart {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

@media (min-width: 78em) {
  #shot-chart-container .shot-chart {
    margin: 0;
  }
}

svg {
  stroke-width: 0.2;
}

svg text {
  font-size: 0.2em;
}

svg text.legend-text {
  font-size: 0.1em;
}

svg rect {
  stroke: black;
  fill: none;
}

svg line {
  stroke: black;
  fill: none;
}

/********END SHOTCHART COURT STYLES****** */
/********SHOT STYLES****** */
svg circle {
  stroke: black;
  fill: none;
}

svg circle.shot.make {
  fill: #49bf8c;
  stroke: white;
  stroke-width: 0.05;
}

svg circle.shot.miss {
  fill: #ed5858;
  stroke: white;
  stroke-width: 0.05;
}

path {
  stroke: white;
  stroke-width: 0.05;
}

path.shot {
  stroke: white;
  fill: none;
  stroke-width: 0.05;
}

/********END SHOT STYLES****** */
/********SHOT TOOLTIPS****** */
.d3-tip {
  line-height: 1;
  font-weight: bold;
  padding: 10px;
  background: #3070b0;
  color: #fff;
  border-radius: 2px;
  pointer-events: none;
}

/* Creates a small triangle extender for the tooltip */
.d3-tip:after {
  box-sizing: border-box;
  display: inline;
  font-size: 10px;
  line-height: 1;
  color: #3070b0;
  position: absolute;
  pointer-events: none;
}

/* Northward tooltips */
.d3-tip.n:after {
  content: '▼';
  margin: -1px 0 0 0;
  top: 100%;
  left: 0;
  text-align: center;
}

/* Eastward tooltips */
.d3-tip.e:after {
  content: '◀';
  margin: -4px 0 0 0;
  top: 50%;
  left: -8px;
}

/* Southward tooltips */
.d3-tip.s:after {
  content: '▲';
  margin: 0 0 1px 0;
  top: -8px;
  left: 0;
  text-align: center;
}

/* Westward tooltips */
.d3-tip.w:after {
  content: '▶';
  margin: -4px 0 0 -1px;
  top: 50%;
  left: 100%;
}
